import AppListNav from './appListNav.vue'
import * as AppResourceApi from '../../api/moudle/appResourceApi'
import UploadApp from './uploadApp.vue'
import {getTeamArr} from '../../mgr/userMgr'

export default {
  data() {
    return {
      currentPlatform: '',
      dataList: [{'flag': 'flag'}],
      queryText: '',
      showUploadView: false,
      file: FileList,
      currentPage: 0,
      teamArr: []
    }
  },
  components: {
    AppListNav, UploadApp
  },
  computed: {
    getTitleActiveClass() {
      if (this.hoverFlag) {
        return `color: red`
      }
    }
  },
  methods: {
    loadAppList() {
      AppResourceApi.getAppList(this.teamArr[0]._id, this.currentPage)
        .then(response => {
          console.log(response)
          this.dataList = [{'flag': 'flag'}]
          response.data.forEach((item) => {
            this.dataList.push(item)
          })
        }, reject => {
          this.$message.error(reject)
        })
    },
    referenceUpload(e) {
      this.file = e.target.files
      if (e.target.files.length > 0) {
        this.showUploadView = true
      }
    },
    closeUploadMethod() {
      this.showUploadView = false
    },
    uploadSuccessMethod() {
      this.showUploadView = false
      this.loadAppList()
    },
    clickIosPlatform() {
      this.currentPlatform = 'ios'
    },
    clickAndroidPlatform() {
      this.currentPlatform = 'android'
    },
    getActiveClass(flag) {
      if (flag === this.currentPlatform) {
        return 'platformActive'
      }
    },
    gotoAppDetail(item) {
      this.$router.push({
        name: 'AppDetail',
        params: {appId: item._id}
      })
    },
    appItemHovered() {
    },
    appItemUnhovered() {
    },
    clickEditorBtn() {
    },
    clickPreview() {
      window.open(window.location.origin + '/appPreView', '_blank')
    }
  },
  created () {
    this.$watch('queryText', () => {
      console.log(this.queryText)
    })
    this.teamArr = getTeamArr()
    if (this.teamArr) {
      this.loadAppList()
    }
  }
}



